<template>
  <el-row>
    <el-col :span="12">
      <MonacoEditor
        height="100vh"
        :code="code"
        theme="vs-dark"
        language="javascript"
        :options="options"
        @mounted="onMounted"
        @codeChange="onCodeChange"
      >
      </MonacoEditor>
    </el-col>
    <el-col :span="12">
      <el-row style="margin-left: 24px; margin-top: 24px">
        <el-button round size="small" @click="jsBeautify">美化</el-button>
        <el-button size="small" round @click="jsMin">压缩</el-button>
        <el-button size="small" round @click="copy">复制</el-button>
        <el-button size="small" round @click="clear">清空</el-button>
        <el-button size="small" type="danger" round @click="eval">
          运行
        </el-button>
      </el-row>
      <el-row style="margin-left: 24px; margin-top: 24px; margin-right: 24px">
        <el-input
          type="textarea"
          :autosize="{ minRows: 2, maxRows: 4 }"
          placeholder="请输入执行函数或者表达式"
          v-model="funcText"
        >
        </el-input>
      </el-row>
      <el-row style="margin-left: 24px; margin-top: 24px; margin-right: 24px">
        <iframe
          id="iframe"
          width="100%"
          height="400px"
          style="overflow: visible; border-width: 0px"
        ></iframe>
      </el-row>
    </el-col>
  </el-row>
</template>

<script>
import MonacoEditor from "vue-monaco-editor-zh";
import { js_beautify, js_min } from "./modules/jsformat";
export default {
  name: "App",
  components: { MonacoEditor },
  data() {
    return {
      code: "",
      options: {
        selectOnLineNumbers: true,
        automaticLayout: true,
        minimap: {
          enabled: false, // 是否启用预览图
        },
      },
      funcText: "",
    };
  },
  methods: {
    onMounted(editor) {
      this.editor = editor;
    },
    onCodeChange(editor) {
      //console.log(this.editor.getValue());
    },
    jsBeautify() {
      var code = this.editor.getValue();
      code = js_beautify(code, 4, " ");
      this.editor.setValue(code);
    },
    jsMin() {
      var code = this.editor.getValue();
      code = js_min("", code, 3);
      this.editor.setValue(code.replace("\n", ""));
    },
    clear() {
      this.editor.setValue("");
      this.funcText = "";
      document.querySelector("#iframe").contentDocument.body.innerHTML = "";
    },
    copy() {
      const aux = document.createElement("input");
      const content = this.editor.getValue();
      aux.setAttribute("value", content);
      document.body.appendChild(aux);
      aux.select();
      document.execCommand("copy");
      document.body.removeChild(aux);
      this.$message({
        showClose: true,
        message: "复制成功！",
        type: "success",
      });
    },
    eval() {
      document.querySelector("#iframe").contentDocument.body.innerHTML = "";
      if (this.funcText == "") {
        document
          .querySelector("#iframe")
          .contentDocument.write(
            '<div style="color: red;word-wrap:break-word;">Error:执行函数或者表达式不能为空</div>'
          );
        return;
      }
      var jsCode = this.editor.getValue();
      jsCode = `<script>
      try {
        ${jsCode}
        var value = ${this.funcText};
        document.write('<div style="color: black;word-wrap:break-word;">'+value+'</div>');
      } catch (error) {
        document.write('<div style="color: red;word-wrap:break-word;">'+error.stack+'</div>');
        console.error(error);
      }
      <\/script>`;
      document.querySelector("#iframe").contentDocument.write(jsCode);
    },
    //'eX83A5CmBQxBv4G2D5Q6RSO1AZrPwjqVVKAQ9C5JAk6woPK6SUtXp%2BF7gV2O5zkyjjPIYihOZyHTZ4dJm4mLdwcOtTNUaUhjq%2BBvG8UdPJ8I%2FSYmrbUTwnd0w1f%2F8Md1vsCUZM1ZezZ8g3gwtpzjwd7OpmX%2BJjy3u1auXN7dWcM%3D'
  },
};
</script>

